import { ref } from 'vue';

class WishlistService {
  // Reactive ÏÉÅÌÉú
  constructor() {
    this.wishlist = ref([]);
    this.loadWishlist();
  }

  // Î°úÏª¨ Ïä§ÌÜ†Î¶¨ÏßÄÏóêÏÑú ÏúÑÏãúÎ¶¨Ïä§Ìä∏Î•º Î°úÎìú
  loadWishlist() {
    const storedWishlist = localStorage.getItem('movieWishlist');
    if (storedWishlist) {
      this.wishlist.value = JSON.parse(storedWishlist);
    }
  }

  // ÏúÑÏãúÎ¶¨Ïä§Ìä∏Î•º Î°úÏª¨ Ïä§ÌÜ†Î¶¨ÏßÄÏóê Ï†ÄÏû•
  saveWishlist() {
    localStorage.setItem('movieWishlist', JSON.stringify(this.wishlist.value));
  }

  // ÏúÑÏãúÎ¶¨Ïä§Ìä∏Ïóê ÏòÅÌôî Ï∂îÍ∞Ä/Ï†úÍ±∞
  toggleWishlist(movie) {
    const index = this.wishlist.value.findIndex(item => item.id === movie.id);

    if (index === -1) {
      // ÏòÅÌôîÍ∞Ä ÏúÑÏãúÎ¶¨Ïä§Ìä∏Ïóê ÏóÜÏúºÎ©¥ Ï∂îÍ∞Ä
      this.wishlist.value.push(movie);
    } else {
      // ÏòÅÌôîÍ∞Ä Ïù¥ÎØ∏ ÏúÑÏãúÎ¶¨Ïä§Ìä∏Ïóê ÏûàÏúºÎ©¥ Ï†úÍ±∞
      this.wishlist.value = this.wishlist.value.filter(item => item.id !== movie.id);
    }

    this.saveWishlist();
  }

  // ÏòÅÌôîÍ∞Ä ÏúÑÏãúÎ¶¨Ïä§Ìä∏Ïóê ÏûàÎäîÏßÄ ÌôïÏù∏
  isInWishlist(movieId) {
    return this.wishlist.value.some(item => item.id === movieId);
  }

  // ÌòÑÏû¨ ÏúÑÏãúÎ¶¨Ïä§Ìä∏ Î∞òÌôò
  getCurrentWishlist() {
    return this.wishlist.value;
  }
}

// Singleton Ïù∏Ïä§ÌÑ¥Ïä§ ÏÉùÏÑ±
const wishlistService = new WishlistService();
export default wishlistService;
